const { registerBlockType } = wp.blocks;

const { Fragment } = wp.element;
const { InspectorControls } = wp.editor;
const { PanelBody,TextControl,Text } = wp.components;

registerBlockType(homebanner_data.suffix+'/home-banner',{
	title:homebanner_data.lang.title,
	icon: 'admin-home',
	category: 'layout',
	supports: {
		color:{
			background:true,
			gradient:false,
			text:false
		}
	},
	attributes:{
		title:{
			type: 'string',
			default: homebanner_data.lang.default_title
		},
		subtitle:{
			type: 'string',
			default: homebanner_data.lang.default_sub_title
		}
	},
	edit:(props)=>{
		return 	<Fragment>
					<div className={props.className} >
						<p>{props.attributes.subtitle}</p>
						<h1>{props.attributes.title}</h1>
					</div>
					<InspectorControls>
						<PanelBody>
							<h3 >{homebanner_data.lang.title}</h3>
							<TextControl
								label={homebanner_data.lang.default_title}
								value={ props.attributes.title }
								onChange={ ( string ) => {  
									props.setAttributes({
										title:string
									})
								}}
							/>
							<TextControl
								label={homebanner_data.lang.default_sub_title}
								value={ props.attributes.subtitle }
								onChange={ ( string ) => {  
									props.setAttributes({
										subtitle:string
									})
								}}
							/>
						</PanelBody>
					</InspectorControls>
				</Fragment>
	},
    save:(props) => {
        return 	<div className={props.className} >
					<p>{props.attributes.subtitle}</p>
					<h1>{props.attributes.title}</h1>
				</div>; 
	},
});